import { ADD_ENTRY } from '../actions/index';

function calculateApp(state = initialState, action) {
  const value = action.text,
    entry = {
      expression: value,
      result: eval(value)
    };

  switch (action.type) {
    case ADD_ENTRY:
      return Object.assign({}, state, {
        items: [
          entry,
          ...state.items
        ]
      })
    default:
      return state
  }
}

export default calculateApp;



