import React, { PropTypes } from 'react';

const Entry = ({ expression, result }) => (
  <li>
    <div>{result}</div>
    <div>{expression}</div>
  </li>
);

Entry.propTypes = {
  expression: PropTypes.string.isRequired,
  result: PropTypes.number.isRequired
}

export default Entry;



