import { connect } from 'react-redux';
import { addEntry } from '../actions/index';
import EntryList from '../components/EntryList';

const mapStateToProps = (state) => {
  return {
    items: state.items
  }
};

const PushEntryList = connect(
  mapStateToProps
)(EntryList);

export default PushEntryList;



